<template><section><html><head></head><body><h1>WallGraphics</h1>
<p>The <code v-pre="">vc-graphics-wall</code> component is used to load a two dimensional wall defined as a line strip and optional maximum and minimum heights. The wall conforms to the curvature of the globe and can be placed along the surface or at altitude. Need to be used as a subcomponent of <code v-pre="">vc-entity</code>.</p>
<h2>Example</h2>
<h3>Load WallGraphics with <code v-pre="">vc-graphics-wall</code></h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-entity :description="description" :wall.sync="wall1">
          <vc-graphics-wall ref="wall1" :positions="positions1" :material="material1" :minimum-heights="[100000.0, 100000.0]"></vc-graphics-wall>
        </vc-entity>
        <vc-entity :description="description" :wall.sync="wall2">
          <vc-graphics-wall ref="wall2" :positions="positions2" :material="material2" :outline="true"></vc-graphics-wall>
        </vc-entity>
        <vc-entity :description="description" :wall.sync="wall3">
          <vc-graphics-wall :positions="positions3" :material="material3" :outline="true" :outline-color="outlineColor3" :maximum-heights="[100000, 200000, 100000, 200000, 100000, 200000, 100000, 200000, 100000, 200000]" :minimum-heights="[0, 100000,  0, 100000, 0, 100000, 0, 100000, 0, 100000]" ref="wall3"></vc-graphics-wall>
        </vc-entity>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
